import { useEffect, useRef, useCallback, useState } from "react";
import freeice from "freeice";
import useStateWithCallback from "./useStateWithCallback";
import socket from "../socket.io";
import ACTIONS from "../socket.io/actions";

export const LOCAL_VIDEO = "LOCAL_VIDEO";

export default function useWebRTC(roomID) {
  //  
  const [clients, updateClients] = useStateWithCallback([]);

  const addNewClient = useCallback(
    // updateClients            clients,       '        .
    (newClient, cb) => {
      //    .  newClient    list      state   useStateWithCallback
      updateClients((list) => {
        // state   useStateWithCallback   newClient     if,       state   state  useStateWithCallback    useEffect   useStateWithCallback       cb(   updateClients)           cbRef.current
        if (!list.includes(newClient)) {
          //       newClient    updateClients      
          return [...list, newClient];
        }

        return list;
      }, cb);
    },
    [clients, updateClients]
  );

  const peerConnections = useRef({}); //peerConnections - '     ,         '              
  const localMediaStream = useRef(null); //localMediaStream            ,    
  const peerMediaElements = useRef({
    //          ,            
    [LOCAL_VIDEO]: null, //        ..
  });

  useEffect(() => {
    //     peer
    async function handleNewPeer({ peerID, createOffer }) {
      if (peerID in peerConnections.current) {
        //      peer     
        return console.warn(`Already connected to peer ${peerID}`);
      }

      peerConnections.current[peerID] = new RTCPeerConnection({
        //       peerConnections
        iceServers: freeice(), //freeice -       .
      });

      peerConnections.current[peerID].onicecandidate = (event) => {
        //      .        offer  answer
        if (event.candidate) {
          //     .
          socket.emit(ACTIONS.RELAY_ICE, {
            // ice candidate
            peerID,
            iceCandidate: event.candidate,
          });
        }
      };

      let tracksNumber = 0; //   2 tracks(  )                       track      tracksNumber
      peerConnections.current[peerID].ontrack = ({
        //    track     
        streams: [remoteStream],
      }) => {
        tracksNumber++; //     track      tracksNumber

        if (tracksNumber === 2) {
          //    tracksNumber === 2     -        .
          // video & audio tracks received
          tracksNumber = 0;
          addNewClient(peerID, () => {
            if (peerMediaElements.current[peerID]) {
              peerMediaElements.current[peerID].srcObject = remoteStream; //            peerId(    )  remoteStream
            } else {
              // FIX LONG RENDER IN CASE OF MANY CLIENTS
              let settled = false;
              const interval = setInterval(() => {
                if (peerMediaElements.current[peerID]) {
                  peerMediaElements.current[peerID].srcObject = remoteStream;
                  settled = true;
                }

                if (settled) {
                  clearInterval(interval);
                }
              }, 1000);
            }
          });
        }
      };

      localMediaStream.current.getTracks().forEach((track) => {
        //...
        // if (track.kind === 'video') {
        //   track.enabled = !track.enabled;
        // }

        //    ,               peerConnections
        peerConnections.current[peerID].addTrack(
          // addTrack      addStream(  ),        localMediaStream.current,               .
          track,
          localMediaStream.current
        );
      });

      if (createOffer) {
        //  createOffer      offer,        -      offer
        const offer = await peerConnections.current[peerID].createOffer(); // offer

        await peerConnections.current[peerID].setLocalDescription(offer); // offer  localDescription -     peerConnections   offer.    setLocalDescription     peerConnections.current[peerID].onicecandidate = (event) => { -                  SDP     - socket.emit(ACTIONS.RELAY_SDP, {  localMediaStream.current.getTracks().forEach((track) => { -  tracks        peerConnections.  localMediaStream.current.getTracks().forEach((track) => { -      ,   socket.emit(ACTIONS.RELAY_SDP, { -  .

        socket.emit(ACTIONS.RELAY_SDP, {
          // SDP-
          peerID,
          sessionDescription: offer, //   offer   offer
        });
      }
    }

    socket.on(ACTIONS.ADD_PEER, handleNewPeer);

    return () => {
      socket.off(ACTIONS.ADD_PEER);
    };
  }, []);

  useEffect(() => {
    //    SESSION_DESCRIPTION
    async function setRemoteMedia({
      peerID,
      sessionDescription: remoteDescription,
    }) {
      await peerConnections.current[peerID]?.setRemoteDescription(
        //  remoteDescription  peerId - sessionDescription     RTCSessionDescription(       sessionDescription            RTCSessionDescription) ..
        new RTCSessionDescription(remoteDescription)
      );

      if (remoteDescription.type === "offer") {
        //  await peerConnections.current[peerID]?.setRemoteDescription(     offer  answer.    remoteDescription  offer      .
        const answer = await peerConnections.current[peerID].createAnswer(); //  offer description

        await peerConnections.current[peerID].setLocalDescription(answer); // remoteDescription  localDesctiption

        socket.emit(ACTIONS.RELAY_SDP, {
          // remoteDescription   RELAY_SDP
          peerID,
          sessionDescription: answer, //  answer    answer
        });
      }
    }

    socket.on(ACTIONS.SESSION_DESCRIPTION, setRemoteMedia);

    return () => {
      socket.off(ACTIONS.SESSION_DESCRIPTION);
    };
  }, []);

  useEffect(() => {
    //    ICE_CANDIDATE
    socket.on(ACTIONS.ICE_CANDIDATE, ({ peerID, iceCandidate }) => {
      peerConnections.current[peerID]?.addIceCandidate(
        new RTCIceCandidate(iceCandidate)
      );
    });

    return () => {
      socket.off(ACTIONS.ICE_CANDIDATE);
    };
  }, []);

  useEffect(() => {
    //   REMOVE_PEER -           REMOVE_PEER,  (   await navigator.mediaDevices.getUserMedia({...)  ..
    const handleRemovePeer = ({ peerID }) => {
      if (peerConnections.current[peerID]) {
        //  peerConnections.current   peerID
        peerConnections.current[peerID].close(); // peerConnections.current[peerID]
      }

      delete peerConnections.current[peerID];
      delete peerMediaElements.current[peerID];

      updateClients((list) => list.filter((c) => c !== peerID));
    };

    socket.on(ACTIONS.REMOVE_PEER, handleRemovePeer);

    return () => {
      socket.off(ACTIONS.REMOVE_PEER);
    };
  }, []);

  useEffect(() => {
    //    .          
    async function startCapture() {
      //  
      localMediaStream.current = await navigator.mediaDevices.getUserMedia({
        //   localMediaStream    navigator.mediaDevices.getUserMedia -      
        audio: true, //    ,  false     
        video: {
          //     false : video: false -     
          //     
          width: 1280, //       1280720
          height: 720,
        },
      });

      addNewClient(LOCAL_VIDEO, () => {
        //         .            LOCAL_VIDEO (  ) -              peerMediaElements   LOCAL_VIDEO
        const localVideoElement = peerMediaElements.current[LOCAL_VIDEO]; //   ' peerMediaElements   LOCAL_VIDEO

        if (localVideoElement) {
          // localVideoElement        html         
          localVideoElement.volume = 0; // 0      .
          localVideoElement.srcObject = localMediaStream.current; //localMediaStream.current -               ,        ,      clients,        
        }
      });
    }

    startCapture() //    socket.emit            
      .then(() => socket.emit(ACTIONS.JOIN, { room: roomID })) //startCapture                       then.
      .catch((e) => console.error("Error getting userMedia:", e)); //          

    return () => {
      //         MainVideoCall .
      localMediaStream.current.getTracks().forEach((track) => track.stop()); //           . .getTracks() -          . .forEach((track) => track.stop()); -   .

      socket.emit(ACTIONS.LEAVE); //                .
    };
  }, [roomID]);

  const provideMediaRef = useCallback((id, node) => {
    peerMediaElements.current[id] = node;
  }, []);

  return {
    clients,
    provideMediaRef,
  };
}
