import { useState, useMemo, useCallback, useEffect } from "react";
import { useParams } from "react-router";
import { Box } from "@mui/system";
import useWebRTC, { LOCAL_VIDEO } from "../../../hooks/useWebRTC";

//   ?      Room =>    useParams,   roomId =>     useWebRTC =>  useWebRTC    navigator.mediaDevices.getUserMedia =>        addNewClient(LOCAL_VIDEO... (        ) =>      return   (index   Room) =>  useWebRTC   localVideoElement     localMediaStream  localVideoElement.srcObject = localMediaStream.current; -     .

function layout(clientsNumber = 1) {
  //       .       ,      -       .     .
  const pairs = Array.from({ length: clientsNumber }).reduce(
    // clientsNumber                
    (acc, next, index, arr) => {
      if (index % 2 === 0) {
        acc.push(arr.slice(index, index + 2));
      }

      return acc;
    },
    []
  );

  const rowsNumber = pairs.length;
  const height = `${100 / rowsNumber}%`;

  return pairs
    .map((row, index, arr) => {
      if (index === arr.length - 1 && row.length === 1) {
        //          
        return [
          {
            width: "100%",
            height,
          },
        ];
      }

      return row.map(() => ({
        //         return
        width: "50%",
        height,
      }));
    })
    .flat(); //     ,          .flat()   .
}

export default function Room() {
  const { id: roomID } = useParams();
  const { clients, provideMediaRef } = useWebRTC(roomID);
  const videoLayout = layout(clients.length);
  const [muted, setMuted] = useState(false);
  const [stopVideo, setStopVideo] = useState(true);

  const createVideoWindows = () => {
    return clients.map((clientID, index) => {
      return (
        <Box key={clientID} style={videoLayout[index]} id={clientID}>
          <video // html tag
            //controls -     
            controls
            // loop -      
            loop={false}
            //poster -         
            poster={"https://pixabay.com/images/id-87928/"}
            //preload -        
            preload="auto"
            width="100%"
            height="100%"
            ref={(instance) => {
              provideMediaRef(clientID, instance);
            }}
            // autoPlay -          
            autoPlay={stopVideo} //   
            playsInline //       
            // muted -      .
            muted={clientID === LOCAL_VIDEO || muted} //             .
          />
        </Box>
      );
    });
  };

  useEffect(() => {
    createVideoWindows();
  }, [stopVideo, muted]);

  return (
    <Box
      style={{
        display: "flex",
        height: "100vh",
        margin: 0,
      }}
    >
      <Box
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          flexWrap: "wrap",
          height: "90vh",
        }}
      >
        {createVideoWindows()}
      </Box>
      <Box
        style={{
          position: "fixed",
          height: "10vh",
          width: "100%",
          bottom: 0,
          background: "gray",
        }}
      >
        <button
          style={{ background: muted ? "red" : "green" }}
          onClick={() => setMuted(!muted)}
        >
          Mute
        </button>
        <button
          style={{ background: stopVideo ? "red" : "green" }}
          onClick={() => setStopVideo(!stopVideo)}
        >
          Video
        </button>
      </Box>
    </Box>
  );
}
