import { useState, useEffect, useRef } from "react";
import socket from "../../../socket.io";
import ACTIONS from "../../../socket.io/actions";
import { v4 } from "uuid"; //v4   
import { useNavigate } from "react-router-dom";
import defaultVideoWebm from "../../../images/Mail_Digital.webm";
import defaultVideoMp4 from "../../../images/Mail_Digital.mp4";
import posterImage from "../../../images/crispus.jpg";

export default function MainVideoCall() {
  const navigate = useNavigate();
  const [rooms, updateRooms] = useState([]);
  const rootNode = useRef();

  useEffect(() => {
    socket.on(ACTIONS.SHARE_ROOMS, ({ rooms = [] } = {}) => {
      //       event   ACTIONS.SHARE_ROOMS,    socket.on -       .
      if (rootNode.current) {
        //   updateRooms     MainVideoCall  
        updateRooms(rooms); //        updateRooms      
      }
    });
  }, []);

  return (
    <div ref={rootNode}>
      <h1>Available Rooms</h1>

      <ul>
        {rooms.map(
          (
            roomID //   
          ) => (
            <li key={roomID}>
              {roomID}
              <button
                onClick={() => {
                  navigate(`/room/${roomID}`); //    
                }}
              >
                JOIN ROOM
              </button>
            </li>
          )
        )}
      </ul>

      <button
        onClick={() => {
          navigate(`/room/${v4()}`); //    
        }}
      >
        Create New Room
      </button>
      <video
        controls
        muted
        autoPlay
        height={400}
        width="100%"
        poster={posterImage}
      >
        {/*  source -     video    src-   video    .      */}
        <source src={defaultVideoWebm} type="video/webm" />
        <source src={defaultVideoMp4} type="video/mp4" />
      </video>
    </div>
  );
}
